import React, { Suspense } from "react";

const LeaderBoard = React.lazy(() =>
  import("../../analytics-new/content/components/leaderBoard")
);

const LeaderboardDashboard = (props) => {
  const { loader, leaderBoard, nevigationRedirect } = props;

  const isgetData = leaderBoard?.data?.length > 0;

  return (
    <div className={`mb-5 col-lg-${isgetData ? '7' : '7'}`}>
      <div className="home_leaderBoard h-100">
        {isgetData || loader ? (
          <Suspense fallback={""}>
            <LeaderBoard
              isDashboard={1}
              loader={loader}
              selectedWallFeed={[]}
              leaderBoard={leaderBoard}
            />
          </Suspense>
        ) : (
          <div className="p-0 card-custom  h-90 card h-100">
            <div className="p-5 card-header">
              <div className="m-0 d-flex align-items-center justify-content-between card-title h5 w-100">
                <h6 className="card-label font-weight-bolder m-0 ">
                 Leader Board
                </h6>
              </div>
            </div>
            <div className="p-10 card-body">
              <div className="not_connected_err not_connected_err_new flex-center overlay-layer d-flex flex-column flex-center h-100">
                <p className="text-dark w-70 text-center font-size-h6">
                Create a feed and discover the most active audience creating content for your event through 
                </p>
                <div className="not_conne_overlay d-flex flex-column flex-center">
                  <a onClick={()=>nevigationRedirect()}
                    className="btn btn-primary"
                    href="#"
                  >
                    Add Feed
                  </a>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default React.memo(LeaderboardDashboard);
